import CApi from "@api";
import {
  CButton,
  CError,
  CInput,
  CInputPassword,
  CLoadingSpinner,
  CPageTitle,
  CSelect,
  CTitle,
} from "@components";
import { FChange, FGetAccess, FSubmitBack } from "@function";
import { Card, Col, Form, Row } from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

const AddUser = () => {
  const router = useRouter();
  const [isSubmit, setIsSubmit] = useState(false);
  const [accessList, setAccessList] = useState({ create: true });
  const [isLoading, setIsLoading] = useState(false);
  const [role, setRole] = useState([]);
  const [errors, setErrors] = useState({});
  const [forms, setForms] = useState({
    id: "",
    email: "",
    id: "",
    name: "",
    role: "",
    password: "",
    password_confirmation: "",
  });

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };

  const handleChange = (e) => {
    FChange(e, forms, setForms);
  };

  const handleSubmit = () => {
    FSubmitBack("api/master/user", forms, setErrors, setIsSubmit, router);
  };

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
    getRole();
  }, []);

  function onRole(value) {
    setForms({
      ...forms,
      role: value,
    });
  }
  const getRole = async () => {
    await CApi.get("api/list-role").then((res) => {
      setRole(res.data);
    });
  };

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.create) return <CError status={403} />;

  return (
    <>
      <CTitle />
      <Card
        cover={
          <Col
            span={24}
            style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
          />
        }
      >
        <CPageTitle />
        <Row>
          <Col span={24}>
            <Form {...formItemLayout}>
              <CInput
                value={forms.name}
                label="Name"
                validate={errors.name}
                name="name"
                id="name"
                isRequired
                onChange={handleChange}
              />
              <CInput
                value={forms.email}
                label="Email"
                validate={errors.email}
                name="email"
                id="email"
                onChange={handleChange}
                style={{ marginTop: -20 }}
              />
              <CInputPassword
                label="Password"
                style={{ marginTop: -20 }}
                id="password"
                value={forms.password}
                validate={errors.password}
                onChange={handleChange}
              />
              <CInputPassword
                label="Password Confirmation"
                style={{ marginTop: -20 }}
                id="password_confirmation"
                value={forms.password_confirmation}
                validate={errors.password_confirmation}
                onChange={handleChange}
              />
              <CSelect
                value={forms.role}
                validate={errors.role}
                id="role"
                name="role"
                label="Role"
                style={{ marginTop: -20 }}
                options={role}
                onChange={onRole}
              />
            </Form>
          </Col>
        </Row>
        <CButton onSubmit={handleSubmit} isSubmit={isSubmit} id={forms.id} />
      </Card>
    </>
  );
};

export default AddUser;
